
import Toast from 'vux/src/components/toast/index.vue'

import {login,sendSmsCode,sendMailCode} from "@/service/userApi"
export default {
name: "login",
components: {Toast},
data(){
return{
userName:"",
password:"",
loadingText: "",
noneTip: false,
showLoading:false,
toastText: "",
uuid:"",
content:'获取验证码',
canClick:true,
totalTime:60,
token:'',
smsCode:'',
imgcode:false,
emailToken:"",
type:""
}
},
methods: {


back(){
this.$router.push('/');
},
loginButton(){

if (!this.userName || !this.password) {
this.toast("账号或密码不能为空")
return;
}
let s = '34qrefefgwrtg24534245gwefgvsdfwrt24@**&%$!ytfthn'
let form = new FormData()
this.loading("正在提交数据...");
form.append('loginName',this.userName)
form.append('password',this.$md5(s+this.password))
form.append('clientId',this.uuid)
form.append('clientType','PC')
login(form).then(res => {
this.showLoading = false;
if (res.data.status == 200) {

this.$store.state.token = res.data.data.token;
this.toast("登录成功");
this.$router.push({path:'/indexM',query:{"loginName":this.userName}})
}else{
this.toast(res.data.message);
}
}).catch(err => {
this.showLoading = false;
console.log(err);
this.toast("网络异常")
})

},
loading(msg) {
this.loadingText = msg;
this.showLoading = true
},
toast(msg) {
this.toastText = msg;
this.noneTip = true;
},
},
created(){
this.uuid = localStorage.getItem('uuid')

}



}



