
import Toast from 'vux/src/components/toast/index.vue'

import {getDownloadUrl} from "@/service/userApi"
export default {
name: "recommendSuccess",

data(){
return{
downloadUrl:'',
toastText:"",
loadingText:"",
noneTip:false,
showLoading:false,
}
},
methods:{
download(){
location.href = this.downloadUrl
}
},
created () {
getDownloadUrl().then(res=>{
if (res.data.status == 200) {
this.downloadUrl = res.data.data.apkUrl
console.log(this.downloadUrl);

}else{
console.log(res.data.messsage);
}
}).catch(err=>{
console.log(err);
})
}
}



